@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
.result{-webkit-flex-basis:100px;flex-basis:100px;padding:5px;border-bottom:2px solid #0e0e0e;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;overflow:hidden}.result__span{text-align:right;font-size:4rem}@media (min-width: 375px) and (min-height: 750px){.result{-webkit-flex-basis:150px;flex-basis:150px}.result__span{font-size:5rem}}@media (min-width: 760px){.result__span{font-size:6rem}}

.panel{-webkit-flex-grow:1;flex-grow:1;border-top:2px solid #0e0e0e;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(5, 1fr)}

.btn{display:block;font-size:3.5rem;color:white;background-color:rgba(18,37,55,0.5);outline:none;border:1px solid #0e0e0e}.btn--result{grid-area:4 / -1 / -1 / -2;background-color:rgba(43,85,128,0.5)}.btn--number{background-color:rgba(23,47,70,0.5)}.btn:active{background-color:rgba(30,60,89,0.5)}@media (min-width: 760px){.btn{font-size:5rem}}@media (min-width: 800px) and (orientation: landscape){.btn{font-size:4rem}}@media (min-width: 1000px) and (orientation: portrait){.btn{font-size:8rem}}@media (min-width: 1024px){.btn{cursor:pointer}.btn:hover{background-color:rgba(11,21,32,0.5)}}

.calculator{width:calc(100% - 20px);height:calc(100% - 20px);background-color:rgba(0,17,34,0.5);border:5px solid #0e0e0e;border-radius:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}@media (min-width: 1024px) and (orientation: landscape){.calculator{width:600px;height:80%}}

.aside{position:fixed;bottom:0;left:0;width:100%;height:90vh;background-color:rgba(17,34,51,0.8);border-radius:50px 50px 0 0;-webkit-transform:translateY(85vh);transform:translateY(85vh);transition:.2s;overflow-y:auto}.aside.active{-webkit-transform:translate(0, 0);transform:translate(0, 0)}.aside__swiper{position:-webkit-sticky;position:sticky;top:0;left:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;padding:6px 0;width:100%;height:5vh;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.aside__line{display:block;width:70px;height:1px;background-color:#fff}.aside__title{position:-webkit-sticky;position:sticky;top:5vh;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);margin:0 auto 10px;width:150px;font-size:3rem;text-align:center;border-bottom:1px solid #fff;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.aside__result{text-align:center;font-size:2rem}@media (orientation: landscape){.aside{-webkit-transform:translateY(84vh);transform:translateY(84vh)}.aside__swiper{height:6vh;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}}@media (min-width: 1024px){.aside{left:50%;width:1024px;height:93vh;background-color:rgba(9,22,36,0.8);-webkit-transform:translate(-50%, 87vh);transform:translate(-50%, 87vh);cursor:pointer}.aside.active{-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0)}.aside__title{font-size:5rem;width:250px}.aside__line{width:200px;height:2px}.aside__result{font-size:3rem}}

*{margin:0;padding:0;box-sizing:border-box}html{color:#fff;font-family:"Rubik",sans-serif;font-size:10px}::-webkit-scrollbar{width:0}body{background:radial-gradient(#234, #123)}.wrapper{width:100%;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

